#room_table {
    width: 64vw;
    height: auto;
    padding-bottom: 4.8vh;
}

#room_box {
    display: table;
    padding: 4vh 2.7vw;
    padding-bottom: 2vh;
}

.room_subbox {
    display: table-cell;
    text-align: center;
    vertical-align: middle;
    width: 50%;
}

#room_info {
    padding: 0 3.2vw;
    margin-right : 2.7vw;
    text-align: left;
}

#room_name {
    font-size: 4.4vh;
    font-weight: 600;
    margin: 0;
}

#room_name_box {
    margin: 1vh 0;
    padding: 0 1vw;
    display: flex;
    width: 100%;
    justify-content: space-between;
}

#room_name_input {
    margin-top: 4vh;
    font-size: 3.2vmin;
    width: 100%;
}

#room_description_input {
    margin-top: 2vh;
    font-size: 3.2vmin;
    width: 100%;
    height: 16vh;
    resize: none;
}

.room_action_image {
    border-radius: 0;
    margin-top: 1vh;
    width : 2vw;
    height : 2vw;
    cursor: pointer;
}

#room_submit_button {
    margin-bottom: 0vh;
}

.room_size_selector {
    width: 6vh;
}

#room_map_subbox {
    padding : 3.2vh 0;
    vertical-align: top;
    font-weight: 300;
}

#room_table_map_box{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    margin-top: 4vh;
    height: 30vh;
}

.room_table_row {
    display: flex;
    width: 100%;
    justify-content: space-evenly;
}

.room_table_icon {
    height: 3vh;
    opacity: 0.5;
}

.room_table_icon:hover {
    opacity: 1;
}

