#item_box {
    display: table;
    width: 64vw;
    height: auto;
    padding: 4.8vh 2.7vw;
}

.item_subbox {
    display: table-cell;
    text-align: center;
    vertical-align: middle;

    width: 50%;
}

#item_info {
    padding: 0 3.2vw;
    margin-right : 2.7vw;
    text-align: left;
}

#item_name {
    font-size: 4.4vh;
    font-weight: 600;
    margin: 0;
}

#item_weight {
    margin : 0;
    margin-top: 1.4vh;
    font-size: 2.2vh;
    font-weight: 300;
}

#item_price {
    margin : 0;
    margin-top : 1.2vh;
    padding-bottom: 3.2vh;
    font-size: 2.2vh;
    font-weight: 300;
    border-bottom: .05vh solid rgb(170, 170, 170);
}

#image {
    height: 56vh;
}

#item_name_box {
    margin: 1vh 0;
    display: flex;
    justify-content: space-between;
}

.item_button {
    margin-top: 4vh;
    background-color: rgb(255, 255, 255);
}

.item_button:hover {
    background-color: rgb(245, 245, 245);
}

.item_action_image {
    border-radius: 0;
    margin-top: 1vh;
    width : 2vw;
    height : 2vw;
    margin-left: 1vw;
    cursor: pointer;
}

#item_description{
    margin-top: 4vh;
    text-align: center;
    font-size: 2.2vh;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 300;
    height : 24vh;
    overflow: scroll;
    white-space: normal;
    width: 100%;
    line-height: 1.4;
    letter-spacing: .08em;
}