.flex {
    display: flex;
    justify-content: space-around;
    margin-top : 6vh;
}

.feature_item {
    text-align: center;
    width: 33%;
}

.feature_detail {
    margin-top: 1.4vh;
    font-size: 3.2vmin;
    font-weight: 400;
}

#greet_background {
    height : 27vh;
    background-size: cover;
    background-image: url('/images/restaurant.jpg');
    transition: all 200ms ease;
    box-shadow: inset 0px 0px 8px 4px rgb(177, 177, 177);
}

#greet_background:hover {
    transition: all 200ms ease;
    box-shadow: inset 0px 0px 12px 5px rgb(177, 177, 177);
}

#greet_container {
    display: table;
    padding-top: 8vh;
    margin: 0 auto;
}

#greet_label {
    display: table-cell;
    vertical-align: middle;
    width: 50vw;
}

#greetings {
    font-size : 4.8vmin;
    margin-top: 0;
    margin-bottom: 0;
}

#greet_username {
    font-size : 2.7vmin;
    margin-top : 1.8vh;
    margin-bottom: 0;
}

#logout_button_outter{
    display: table-cell;
    vertical-align: middle;
    text-align: center;
}

#user_table {
    width: 60%;
    height: auto;
}

#button_box {
    margin: 0 auto;
    margin-top: 13vh;
    display: flex;
    justify-content: center;
}

.user_action_button {
    display: inline-block;
    margin: auto 2vw;
    width: 10vw;
}


