#pagingButtonsContainer {
    font-weight : 300;
    font-size : 2.7vmin;
    text-align : center;
    margin-top : 5vh;
}

#pageSelector {
    width : 3.2vw;
}

.arrow_button {
    height: 2.8vmin;
    width: 2.8vmin;
    text-align: center;
    margin-left : 4%;
    margin-right : 4%;
    padding: 1%;
    border-radius: 50%;
    display: inline-block;
    transition: all 200ms ease;
    cursor: pointer;
}

.arrow_button:hover {
    transition: all 200ms ease;
    background-color: rgb(232, 232, 232);
}
