#rooms_table {
    height: auto;
    padding: 6vh 8vw;
    padding-top : 1vh;
    margin-bottom: 2vh;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
}

#room_label{
    font-size: 5vmin;
}

.room_label_box {
    padding : 0 4vw;
    display: flex;
    justify-content: space-between;
}

.room_action_button_outter{
    margin: auto 0;
    vertical-align: bottom;
}

.rooms_row {
    margin-top: 4vh;
    display: flex;
    justify-content: space-evenly;
    height: 16vh;
}

.rooms_second_row {
    margin-top: 8vh;
    margin-bottom: 8vh;
}

.room_cell {
    padding : 2vh 2vw;
    height: 100%;
    width: 12vw;
}

.room_name_label {
    font-size: 4vmin;
}

.room_tables_label {
    margin-top: 1vh;
    font-weight: 300;
}

.room_description_label {
    margin-top: 1.6vh;
    overflow: scroll;
    height : 6vh;
}
