.form_input {
    width: 94%;
    height : 5vmin;
    font-size: 3vmin;
    margin-top: 2vh;
    line-height: 150%;
    resize: none;
}

.form_selector {
    width : 100%;
    margin-top : 2vh;
    font-size : 3vmin;
}

.form_footer {
    text-align: center;
    font-size: 2vmin;
    font-weight : 300;
    margin: 4vh 0;
    margin-bottom: 2.7vh;
}

.form {
    margin: 0 auto;
    width: 30vw;
    overflow: hidden;
    padding: 1vh 2vw;
    padding-top : 0;
}

.form_info {
    margin: 0 auto;
    width: 30vw;
    margin-bottom: 2vh;
    overflow: hidden;
    padding: 0 2vw;
}

.form_info_text {
    font-size: 2.4vmin;
    font-weight: 300;
}

.form_red {
    color: red;
}

.form_green {
    color: green;
}

.form_legend {
    font-size: 4.8vmin;
    margin-top: 4vh;
    margin-bottom: 2%;
}

.form_text_area {
    vertical-align: top;
    height: 16vmin;
}

.form_inputs_container {
    display: flex;
    justify-content: flex-start;
}

.form_inline {
    display: inline-block;
    margin : 0 1.6vw;
}

.form_inline_last {
    margin-right : 1vw;
}