body {
  margin: 0;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  height : 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(245, 245, 245);
  transition: all 200ms ease;
}

img {
  border-radius: 2vmin;
}

.shadow {
  transition: all 200ms ease;
  box-shadow: 0px 4px 8px 4px rgb(177, 177, 177);
}

.shadow:hover {
  transition: all 200ms ease;
  box-shadow: 0px 4px 9px 5px rgb(177, 177, 177);
}

.light_shadow {
  transition: all 200ms ease;
  box-shadow: 0px 2px 3px 2px rgb(200, 200, 200);
}

.light_shadow:hover {
  transition: all 200ms ease;
  box-shadow: 0px 2px 5px 2.5px rgb(200, 200, 200);
}

.card_view {
  background-color: rgb(250, 250, 250);
  border-radius: 3vmin;
  text-align : center;
}

.no_link {
  color:inherit;
  text-decoration: none;
}

.emphasized {
  font-weight: 600;
}

.table {
  margin: 0 auto;
  width: 70%;
  height: 64.8vh;
}

.smallMarginTop {
  margin-top: 2.7vh;
}

.midMarginTop {
  margin-top: 6vh;
}

.largeMarginTop {
  margin-top: 16vh;
}

.text_container {
  font-size: large;
  line-height: 1.6;
  font-weight: 300;
  text-align: center; 
}

.text_container_title {
  font-size: 220%;
  margin-top: 0.8vh;
  margin-bottom: 1.6vh;
}

.button {
  margin: 0 auto;
  text-align: center;
  padding: 1.6vh 2.7vw;
  border-radius: 2vmin;
  background-color: #e6e6e6;
  opacity: 0.8;
  transition: all 200ms ease;
  font-weight: 300;
  font-size: 1.2rem;
  cursor: pointer;
}

.button:hover {
  transition: all 200ms ease;
  background-color: rgb(225, 225, 225);
}

.button_outter {
  margin: 3vh auto;
  width: 16vw;
}

.center {
  text-align: center;
  position: absolute;
  top: 48%;
  left: 50%;
  -ms-transform: translateY(-48%) translateX(-50%);
  transform: translateY(-48%) translateX(-50%);
}

.background {
  background-image: url("/images/restaurant.jpg");
  background-size: cover;
}

.big_label {
  font-size: 4.8vmin;
  margin-top: 4vh;
  margin-bottom: 4vh;
}

.input_field {
  background-color: rgb(235, 235, 235);
  border: none;
  outline: none;
  padding-top: 0.8vh;
  padding-bottom: 0.8vh;
  padding-left: 1.1vw;
  padding-right: 0.5vw;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  border-radius: 1vmin;
  transition: all 200ms ease;
}

.input_field:hover {
  background-color: rgb(232, 232, 232);
  transition: all 200ms ease;
}

.first_table_row {
  margin-top: 3vh;
  margin-bottom: 2vh;
}

.table_row {
  width: 100%;
  transition: all 200ms ease;
  background-color: rgb(235, 235, 235);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.table_row:hover {
  transition: all 200ms ease;
  background-color: rgb(228, 228, 228);
}

.table_cell {
  display:table-cell;
  padding : 1.8vh 2.2vw;
}

.faded {
  font-weight: 100;
  color: rgb(128, 128, 128);
}
