#item_container {
    display: table;
    margin-top: 3.2vh;
    height: 50vh;
    width: 100%;
}

.item_box {
    display: table-cell;
    margin: 0 .8vw;
    text-align: center;
}

.item_name {
    margin-top: 2vh;
    font-size : 4.2vh;
}

#item_table {
    height: auto;
    padding: 6vh 8vw;
    padding-top : 5.6vh;
    margin-bottom: 2vh;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
}

.item_description {
    margin: 0 auto;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    margin-top: 1vh;
    font-size : 2vh;
    font-weight: 300;
    overflow: scroll;
    height: 13.8vh;
    width: 18vw;
    white-space: pre-line;
    text-align: center;
    line-height: 1.4;
    letter-spacing: .05em;
}

#item_image {
    height: 35vh;
    display: block;
    margin: 0 auto;
}

#search_container{
    display: flex;
    vertical-align: middle;
}

#search_field {
    font-size: 3vmin;
    height: 4.8vh ;
}

#search_submit_button {
    margin: auto;
    margin-left: 1vw;
    height : 2.5vh;
    width: auto;
}

.item_info {
    padding: 0 2vw;
}

#add_new_item_button{
    display: table-cell;
    display: inline-block;
    width: 12vw;
    margin-right : 1.6vw;
    padding : 1.9vh 1vw;
}