nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 8vh;
    background-color: rgba(255,255,255);
    z-index: 32;
    position: relative;
}

.menu_block {
    font-weight: 300;
    display: table-cell;
    vertical-align: middle;
    text-align: center;
    color: rgb(56, 56, 56);
    padding-left: 1.6vw;
    padding-right: 1.6vw;
}

.menu_block:hover {
    transition: all 200ms ease;
    color: rgb(128, 128, 128);
}

#container {
    display: table;
    height: 100%;
    margin-right: 4vw;
}

#main_label {
    font-weight: 600;
    font-size: 4vh;
    margin-left : 16vw;
}

