#order_table {
    height: auto;
    padding: 6vh 8vw;
    padding-top : 1vh;
    margin-bottom: 2vh;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
}

#order_container {
    display: flex;
    justify-content: space-between;
    margin-top: 2vh;
    height: 50vh;
    width: 50vw;
}

#order_label{
    font-size: 5vmin;
}

.order_box_outter {
    display: table-cell;
    text-align: center;
    padding : 0 1vw;
}

.order_box_inner {
    text-align: center;
    width: 18vw;
    height : 45vh;
    padding : 2.7vh 1.6vw;
    overflow: hidden;
    position: absolute;

}

.order_item {
    display: flex;
    justify-content: space-between;
    text-align: left;
    padding: 1vh 1vw;
    font-weight: 300;
    margin-bottom : 1vw;
}

.order_item_box {
    overflow: scroll;
    padding: .2vh .4vw;
    height: 38.8vh;
}

.order_total_price_box {
    padding : 0 1.2vw;
    display: flex;
    justify-content: space-between;
    font-size: 3.2vmin;
    font-weight: 200;
    text-align: right;
}

.order_price {
    font-weight: 600;
}

.order_label_box {
    padding : 0 4vw;
    display: flex;
    justify-content: space-between;
}

.order_action_button_outter{
    margin: auto 0;
    vertical-align: bottom;
}

.order_delete_layout {
    width: 21.2vw;
    height: 50vh;
    position: absolute;
    display: table;
    z-index: 1;
    border-radius: 3vmin;
    background-color: rgb(255, 255, 255, 0.5);
    opacity: 0;
    cursor: pointer;
    transition: all 200ms ease;
}

.order_delete_layout:hover {
    opacity: 1;
}

.order_delete_image_box {
    width: 100%;
    height: 100%;
    display: table-cell;
    vertical-align: middle;
}

.order_delete_image {
    width: 4vw;
    margin: auto;
    
}