#order_main_box {
    width: 30vw;
    height: auto;
    padding: 3vh 2vw;
}

#order_item_box {
    margin-top : 1.6vh;
    padding : 1.6vh 1.6vw;
    font-weight: 300;
    font-size: 2.7vmin;
    display: flex;
    justify-content: space-between;
}

#no_items_label {
    display: table-cell;
    vertical-align: middle;
    font-weight: 300;
    line-height: 1.6;
    font-size: 3.2vmin;
}

#no_items_label_box {
    margin: auto 4vw;
    display: table;
    height: 100%;
}

#order_item_list {
    padding: .5vh 1vw;
    overflow: scroll;
    height: 48vh;
}

#order_total_price_box {
    display: flex;
    justify-content: space-evenly;
    font-size: 4vmin;
}